import { useEffect, useState } from 'react';
import { AboutApi } from 'utils/Api';

const About = () => {
  const [about, setAbout] = useState(null);
  useEffect(() => {
    AboutApi().then((res) => {
      setAbout(res.data.detail);
    });
  }, []);
  return <div dangerouslySetInnerHTML={{ __html: about }} className="p-4 tracking-widest leading-6"></div>;

};

export default About;
